<template>
  <v-container app class="wash_code container c_box">
    <div class="page wash_code">
      <space-ui v-if="isSkeleton" sign="washCode" />
      <template v-else>
        <div class="wash_box">
          <div class="wash_title">
            <span class="lt">{{ $t('UI_VIP_Embaralhar.moneyTip') }}</span>
            <div class="rt">
              <!-- <a v-if="$store.state.base.systemConfig.show_gameplay_statistics" @click="isShowStatistic = true">
                {{ capitalizeFirstLetter($t('UI_Wash_Code_Statistics_title_1')) }}
              </a> -->
              <a @click="historyState = true">{{ $t('UI_Activity_Details') }}</a>
            </div>
          </div>
          <div class="wash_money">
            <div class="lt">
              <div class="lt_img" v-image="'gold_medal'"></div>
              <div class="lt_level">
                <span class="text">{{ $t('UI_VIP_Current_Livel') }}</span>
                <span class="level">&nbsp;{{ $t('UI_VIP_TAB.Vip') }}{{ getVipCurrentLevel() }}</span>
              </div>
            </div>
            <div class="rt">
              <span class="tt">{{ currencySymbol }}&nbsp;{{ receiveMoney }}</span>
              <v-btn
                class="bt btn_radius"
                color="t_btn_host_color"
                block
                :class="{ disabled_btn: receiveMoney <= 0 }"
                @click="cleanBet"
                v-zoom="receiveMoney >= 0"
              >
                <auto-font
                  uuid="btnWashIds"
                  :level="2"
                  :maxFont="14"
                  :text="$t('UI_VIP_Embaralhar.receberBtn')"
                ></auto-font>
              </v-btn>
            </div>
          </div>
        </div>
        <div v-if="isMultiple" class="wash_multiple">
          <div class="wash_multiple_title">{{ gameData.lang_title || '' }}</div>
          <div class="wash_multiple_time" v-if="multipleTime">{{ multipleTime }}</div>
          <div class="wash_multiple_text" v-html="gameData.content"></div>
        </div>
        <div class="wash_text_table">
          <div class="wash_text">
            <icon-font
              v-if="!isApp"
              class="wash_text_table_icon_top"
              name="icon-icon_email_chip"
              :size="variables.wash_text_table_icon_top_size"
            ></icon-font>
            <icon-font
              v-if="!isApp"
              class="wash_text_table_icon_left"
              name="icon-icon_email_chip"
              :size="variables.wash_text_table_icon_left_size"
            ></icon-font>
            <template v-if="isApp">
              <span>{{ $t('UI_VIP_Embaralhar.desc3') }}</span>
              <span class="wash_text_title">{{ $t('UI_VIP_Embaralhar.desc5') }}</span>
            </template>
            <template v-else>
              <span>{{ $t('UI_VIP_Embaralhar.desc1') }}</span>
            </template>
          </div>
          <div class="wash_table">
            <table-temp :table-head="headList" :table-list="tableList">
              <template slot="level" slot-scope="{ row }">
                <div class="level_text">
                  <span :class="{ text: row.grade == currentLevel }">{{ row.level }}</span>
                  <span class="current" v-if="row.grade == currentLevel">
                    {{ $t('UI_VIP_Current_Livel') }}
                  </span>
                </div>
              </template>
            </table-temp>
          </div>
        </div>
      </template>

      <!-- 奖励历史弹窗 -->
      <custom-dialog v-model="historyState" :full="true">
        <recomendar-history :style="{ width: isApp ? '100%' : '720px' }" @close="historyState = false" />
      </custom-dialog>

      <!-- 家游玩记录统计 -->
      <custom-dialog v-model="isShowStatistic" :full="true">
        <statistic-info
          :style="{ width: isApp ? '100%' : '720px' }"
          @close="isShowStatistic = false"
          :show="isShowStatistic"
        />
      </custom-dialog>
    </div>
  </v-container>
</template>

<script>
import { mapGetters, mapState } from 'vuex'
import ContainerMixin from '@/components/containerMixin'
import tableTemp from '@/components/ui/table-temp'
import RecomendarHistory from '@/components/ui/recomendar-history'
import StatisticInfo from '@/components/ui/statistic-info/index.vue'
import { info, cleanBetV2, newPercentList, newGamePromotion } from '@/api/modules/bet.js'
import { PLATFORM_VALUE } from '@/api/setting.js'
import spaceUi from '@/components/skeleton/space-ui.vue'
import PublishSubscribe from 'publish-subscribe-js'
import variables from '@style/views/wash-code.modules.scss'
import { capitalizeFirstLetter } from '@/utils/helper.js'

export default {
  name: 'washCode',
  components: {
    RecomendarHistory,
    tableTemp,
    spaceUi,
    StatisticInfo
  },
  mixins: [ContainerMixin],
  data() {
    return {
      bonusInfo: {}, // 洗码金额信息
      percentData: {}, // 表信息
      gameData: {}, // 洗码倍率信息
      historyState: false, // 奖励历史弹窗
      timer: null, // 延迟显示loading
      isSkeleton: true, // 骨架屏显示,
      multipleTime: 0, //洗码倍率信息时间
      variables,
      isShowStatistic: false // 统计信息显示
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('vip', ['vipList', 'infoDesc']),
    ...mapState('switchState', ['currencyShow']),
    ...mapGetters('base', ['getVipCurrentLevel', 'isBetShow']),
    ...mapState('base', ['showRedDot']),
    // 当前等级
    currentLevel() {
      return this.user.vip_id - 1
    },
    // 金额标识
    currencySymbol() {
      return this.currencyShow ? this.systemConfig.currency_symbol : ''
    },
    // 当前用户的VIP可领取洗码金额
    receiveMoney() {
      // const currentVip = this.getBonusInfo(this.user.vip_id);
      // const goldArr = currentVip.map((v) => v.gold);
      // const num = goldArr.reduce((a, b) => this.$u.formatterNumber(a) + this.$u.formatterNumber(b), 0) || 0;

      // return parseFloat(num).toFixed(2);
      const { clean_bet = {} } = this.showRedDot
      const { value = 0 } = clean_bet
      return parseFloat(value).toFixed(2)
    },
    // 获取表头
    newHead() {
      const { list = [] } = this.bonusInfo
      const level = {
        label: this.$t('UI.MakeMoney_1_MakeGold_1'),
        prop: 'level',
        width: '100px',
        fixed: 'left',
        type_id: -999, // 标识是等级
        slot: 'level',
        align: 'center'
      }
      const head = list
        .sort((a, b) => b.sort - a.sort)
        .map(item => {
          const obj = {
            width: this.isApp ? '100px' : '130px',
            label: item.name,
            prop: this.$u.camelCase(item.name),
            sort: item.sort,
            type_id: item.type_id,
            align: 'center'
          }
          return obj
        })

      return [level, ...head]
    },
    // 拼装表头
    headList() {
      const {
        wash_table_size,
        wash_table_bold,
        wash_table_radius,
        wash_table_zebra,
        wash_table_is_scroll,
        wash_table_head_bg,
        wash_table_head_radius,
        wash_table_head_text_color,
        wash_table_head_size,
        wash_table_head_bold,
        wash_table_head_align,
        wash_table_head_margin,
        wash_table_head_padding_app,
        wash_table_head_padding_pc,
        wash_table_head_fixed,
        wash_table_hor_bg,
        wash_table_hor_app_padding,
        wash_table_hor_pc_padding,
        wash_table_hor_border_t,
        wash_table_hor_border_r,
        wash_table_hor_border_b,
        wash_table_hor_border_l,
        wash_table_hor_margin,
        wash_table_hor_color,
        wash_table_hor_size,
        wash_table_hor_bold,
        wash_table_hor_align
      } = variables
      return {
        size: {
          fontSize: wash_table_size,
          fontWeight: wash_table_bold
        },
        radius: wash_table_radius,
        zebra: wash_table_zebra,
        isScroll: !(wash_table_is_scroll == false + ''),
        head: {
          bg: wash_table_head_bg,
          color: wash_table_head_text_color,
          radius: wash_table_head_radius,
          size: {
            fontSize: wash_table_head_size,
            fontWeight: wash_table_head_bold,
            textAlign: wash_table_head_align
          },
          margin: wash_table_head_margin,
          padding: this.isApp ? wash_table_head_padding_app : wash_table_head_padding_pc,
          fixed: !(wash_table_head_fixed == false + '')
        },
        hor: {
          bg: wash_table_hor_bg,
          padding: this.isApp ? wash_table_hor_app_padding : wash_table_hor_pc_padding,
          border_t: wash_table_hor_border_t,
          border_r: wash_table_hor_border_r,
          border_b: wash_table_hor_border_b,
          border_l: wash_table_hor_border_l,
          margin: wash_table_hor_margin,
          color: wash_table_hor_color,
          size: {
            fontSize: wash_table_hor_size,
            fontWeight: wash_table_hor_bold,
            textAlign: wash_table_hor_align
          }
        },
        columns: this.newHead
      }
    },
    // 获取列表数据
    tableList() {
      let tableList = []
      const { list = {} } = this.percentData
      const newList = JSON.parse(JSON.stringify(this.$u.lodash.isObject(list) ? list : {}))

      // 把配置的数据插入列表里
      this.newHead.forEach(head => {
        if (head.type_id == -999) {
          const firstItem = Object.values(newList)[0] || []
          firstItem.forEach((first, i) => {
            tableList[i] = tableList[i] ? tableList[i] : {}
            tableList[i][head.prop] = first.name.toLocaleUpperCase()
            tableList[i]['grade'] = i
          })
        } else {
          const newItem = newList[head.type_id] || []
          newItem.forEach((item, i) => {
            tableList[i] = tableList[i] ? tableList[i] : {}
            tableList[i][head.prop] = `${Number(item.percent * 100).toFixed(2)}%`
          })
        }
      })

      return tableList
    },
    // 判断倍率是否显示
    isMultiple() {
      const { cur_time = 0, end_time_stamp = 0 } = this.gameData

      return end_time_stamp > cur_time
    }
  },
  created() {
    // 判断洗码是否有开启，未开启返回首页
    if (!this.isBetShow) return this.$router.push({ name: 'menu' })

    this.getList()
    // 请求洗码红点
    this.$store.dispatch('base/showRedDotReceivedFun', {
      language: this.languageType,
      token: this.user.token,
      version: 2,
      event: 'clean_bet'
    })
  },
  methods: {
    capitalizeFirstLetter,
    // 获取表格信息
    async getList() {
      let state = true
      this.isSkeleton = true

      const params = {
        token: this.user.token,
        language: this.languageType
      }
      const par = {
        token: this.user.token,
        language: this.languageType,
        platform: PLATFORM_VALUE
      }

      this.timer = setTimeout(() => {
        if (state) this.$pop.loading().show()
      }, 2000)
      const [resInfo, resList, resGame] = await Promise.all([
        info(par).catch(e => e), // 获取洗码领取信息
        newPercentList(params).catch(e => e), // 获取洗码表格信息
        newGamePromotion(params).catch(e => e) // 获取洗码倍率信息
      ])
      state = false
      if (this.timer) clearTimeout(this.timer)
      this.$pop.loading().hide()

      // 处理洗码领取金额
      const { code, data = {} } = resInfo
      if (code == 0) this.bonusInfo = data

      // 处理洗码表格数据
      const { code: percentCode = '', data: percentData = {} } = resList
      if (percentCode == 0) this.percentData = percentData

      // 获取洗码倍率信息
      const { code: gameCode = '', data: gameData = {} } = resGame
      if (gameCode == 0) this.gameData = gameData

      // 处理洗码倍率信息时间
      this.multipleTimeFun()

      this.isSkeleton = false
    },
    // 洗码, 大于等于1才能点击
    async cleanBet() {
      if (this.receiveMoney <= 0) return
      if (this.receiveMoney < this.bonusInfo?.min_clean_bet_limit) {
        this.$pop.message().show({ content: this.$t('UI.Wash_Tips', { gold: this.bonusInfo?.min_clean_bet_limit }) })
        return
      }

      const params = { language: this.languageType, token: this.user.token }
      const { code, msg } = await cleanBetV2(params).catch(e => e)
      if (code != 0) return this.$pop.message().show({ content: msg })

      // 刷新数据
      this.getList()

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        params: {
          title: this.$t('UI_Code_WashCodeRecord'),
          value: this.receiveMoney
        }
      })

      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 2,
        event: 'clean_bet',
        language: this.$store.state.base.languageType,
        token: this.$store.state.base.user.token
      })
      // 更新洗码待领取弹窗
      localStorage.removeItem(`IS_CLEAN_BET_${this.user.uid}`)
    },
    // 处理 VIP 专项信息
    getBonusInfo(vipId) {
      const { list = [], vip_conf = {} } = this.bonusInfo
      return this.getVIPList(list, vip_conf, vipId)
    },
    getVIPList(list = [], conf = {}, id = '') {
      let t = list.lenght > 0 ? list.sort((a, b) => (a.sort > b.sort ? 1 : -1)) : list
      t = t.map(item => {
        let current = conf[id][item.type_id]
        current = parseFloat(current * 100).toFixed(2)
        return {
          ...item,
          current: current
        }
      })
      return t.map(item => ({
        name: item.name,
        current: `${item.current}%`,
        bet: (this.user.vip_id == id ? this.getNumberValue(item.list, 'bet') : 0).toFixed(2),
        gold: (this.user.vip_id == id ? this.getNumberValue(item.list, 'gold') : 0).toFixed(2)
      }))
    },
    getNumberValue(list, key) {
      const newList = list.map(v => v[key])
      const num = newList.reduce((a, b) => this.$u.formatterNumber(a) + this.$u.formatterNumber(b))

      return parseFloat(num)
    },
    // 处理洗码倍率信息时间
    multipleTimeFun() {
      const { cur_time = 0, end_time_stamp = 0 } = this.gameData
      if (!cur_time || !end_time_stamp) return

      const time = this.$u.datetime.getDifferTime(end_time_stamp * 1000, cur_time * 1000)
      const { day, hh, mm, ss } = time
      if (day >= 1) {
        this.multipleTime = this.$t('UI_Wash_Multiple', { X: day, H: hh })
      } else {
        this.multipleTime = `${hh}:${mm}:${ss}`

        const second = hh * (60 * 60) + mm * 60 + Number(ss)
        this.multipleTimeCount(second)
      }
    },
    // 时间倒计时
    multipleTimeCount(second = 0) {
      if (this.multipleTimeout) clearTimeout(this.multipleTimeout)
      if (!second || second < 0) return

      this.multipleTimeout = setTimeout(() => {
        const newSecond = second - 1
        // 相差的时间/时
        const hh = Math.floor(newSecond / (60 * 60))
        // 相差的时间/分
        let mm = newSecond - 60 * 60 * hh
        mm = Math.floor(mm / 60)
        // 相差的时间/秒
        let ss = newSecond - 60 * 60 * hh - 60 * mm

        this.multipleTime = `${this.$u.datetime.strpad(hh)}:${this.$u.datetime.strpad(mm)}:${this.$u.datetime.strpad(ss)}`

        this.multipleTimeCount(newSecond)
      }, 1 * 1000)
    }
  },
  beforeDestroy() {
    if (this.multipleTimeout) clearTimeout(this.multipleTimeout)
  }
}
</script>

